class Files extends React.Component {
  static propTypes = {
    classes: PropTypes.object
  };

  state = {
    selectedFilter: 'all',
    selectedCert: null
  };

  handleFilterChange = (filter) => {
    this.setState({ selectedFilter: filter });
  };

  handleCertClick = (certImage) => {
    this.setState({ selectedCert: certImage });
  };

  handleCloseModal = () => {
    this.setState({ selectedCert: null });
  };

  filterItems = (items, filter) => {
    if (filter === 'all') return items;
    return items.filter(item => {
      if (Array.isArray(item.type)) {
        return item.type.includes(filter);
      }
      return item.type === filter;
    });
  };

  render() {
    const { classes } = this.props;
    const { selectedFilter, selectedCert } = this.state;

    const filterTypes = [
      { value: 'all', label: 'All' },
      { value: 'certification', label: 'Certifications' },
      { value: 'milestone', label: 'Milestones' },
      { value: 'experience', label: 'Experience' },
      { value: 'content', label: 'Content' }
    ];

    const filteredItems = this.filterItems(timelineData, selectedFilter);

    return (
      <Main className={classes.root}>
        <Secuence stagger>
          <h1><Text>Journey Timeline</Text></h1>

          <div className={classes.filterSection}>
            {filterTypes.map(type => (
              <button
                key={type.value}
                className={`${classes.filterButton} ${selectedFilter === type.value ? classes.active : ''}`}
                onClick={() => this.handleFilterChange(type.value)}
              >
                <Text>{type.label}</Text>
              </button>
            ))}
          </div>

          <div className={classes.timelineContainer}>
            {filteredItems.map((item, index) => (
              <Fader key={index} className={classes.timelineItem}>
                <div className={`${classes.timelineContent} ${item.highlight ? 'highlight' : ''}`}>
                  <div className={classes.timelineHeader}>
                    {item.icon && (
                      <img 
                        src={item.icon} 
                        alt={item.title}
                        className={`${classes.timelineIcon} ${item.highlight ? 'highlight' : ''}`}
                      />
                    )}
                    <div>
                      <div className={`${classes.timelineTitle} ${item.highlight ? 'highlight' : ''}`}>
                        {item.link ? (
                          <Link href={item.link}>
                            <Text>{item.title}</Text>
                          </Link>
                        ) : (
                          <Text>{item.title}</Text>
                        )}
                      </div>
                      {item.organization && (
                        <div className={classes.timelineOrg}>
                          <Text>{item.organization}</Text>
                        </div>
                      )}
                      <div className={classes.timelineDate}>
                        <Text>{item.date}</Text>
                      </div>
                    </div>
                  </div>
                  <div className={classes.timelineDesc}>
                    <Text>{item.description}</Text>
                  </div>
                  {item.embed && (
                    <div className={classes.embedContainer}>
                      {item.embed.type === 'youtube' ? (
                        <iframe
                          src={`https://www.youtube.com/embed/${item.embed.id}`}
                          title={item.title}
                          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                          allowFullScreen
                        />
                      ) : item.embed.type === 'video' && (
                        <video
                          src={item.embed.source}
                          autoPlay={item.embed.autoplay}
                          loop={item.embed.loop}
                          muted={item.embed.muted}
                          playsInline={item.embed.playsinline}
                          className={classes.videoEmbed}
                          preload="auto"
                          defaultMuted
                          webkit-playsinline="true"
                          onLoadedData={(e) => {
                            e.target.play().catch(err => console.log('Video autoplay failed:', err));
                          }}
                        />
                      )}
                    </div>
                  )}
                  {item.type === 'certification' && (
                    <img
                      src={item.certImage}
                      alt={`${item.title} Certificate`}
                      className={classes.certImage}
                      onClick={() => this.handleCertClick(item.certImage)}
                    />
                  )}
                </div>
                <div className={`${classes.timelineDot} ${item.highlight ? 'highlight' : ''}`} />
              </Fader>
            ))}
          </div>
        </Secuence>

        {selectedCert && (
          <div 
            className={classes.certModal}
            onClick={this.handleCloseModal}
          >
            <img
              src={selectedCert}
              alt="Certificate"
              className={classes.certModalImage}
              onClick={e => e.stopPropagation()}
            />
          </div>
        )}
      </Main>
    );
  }
}

export default withStyles(styles)(Files);
